import React from 'react';
import AddOption from './AddOption';
import Header from './Header';
import Options from './Options';
import Action from './Action'
import OptionModal from './OptionModal';

export default class IndecisionApp extends React.Component {
    state = {
        options: [],
        selectedOption: undefined
    }
    componentDidMount() {

        try {
            const json = localStorage.getItem('options');
            const options = JSON.parse(json);
    
            if (options) {
                this.setState(() => ({ options }));
            }
        } catch(e) {
            // Do nothing at all
        }
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevState.options.length !== this.state.options.length) {
            const json = JSON.stringify(this.state.options)
            localStorage.setItem('options', json)
        }
    }
    componentWillUnmount() {
        console.log('componentWillUnmount');
    }
    handleDeleteOptions = () => {
        this.setState(() => ({ options: [] }));
    }
    handleDeleteOption = (optionToRemove) => {
        this.setState((prevState) => ({
            options: prevState.options.filter((option) => optionToRemove !== option)
        }));
    }
    clearSelectedOption = () => {
        this.setState(() => ({ selectedOption: undefined }));
    }
    handlePick = () => {
        let randomOption = Math.floor(this.state.options.length * Math.random())
        const option = this.state.options[randomOption]
        this.setState(() => ({ selectedOption: option }));
    }
    handleAddOption = (option) => {

        if (!option) {
            return 'Insira uma opção válida.'
        } else if (this.state.options.indexOf(option) > -1) {
            return 'A opção já existe'
        }
        
        this.setState((prevState) => ({ 
            options: prevState.options.concat([option]) 
        }));
    }
    render() {
        const title = 'Indecisão APP';
        const subtitle = 'Coloque sua escolha nas mãos do computador.';
        return (
            <div>
                <Header title={title} subtitle={subtitle}/>
                    <div className="container">
                        <Action 
                            hasOptions={this.state.options.length > 0}
                            handlePick={this.handlePick}
                        />
                        <div className="widget">
                            <Options 
                            options={this.state.options}
                            handleDeleteOptions={this.handleDeleteOptions}
                            handleDeleteOption={this.handleDeleteOption}
                            />
                            <AddOption handleAddOption={this.handleAddOption}/>                        
                        </div>
                        
                        <OptionModal 
                            selectedOption={this.state.selectedOption}
                            clearSelectedOption={this.clearSelectedOption}
                        />
                    </div>
            </div>
        );
    }
}